interface Products {
    id: number
    name: string
    price: number
    amount: number
}

const mockProducts:Products[] = [
    {
        id:1,
        name: '',
        amount: 10,
        price: 150
    },
    {
        id: 2,
        name: '  ',
        amount: 10,
        price: 100
    },
    {
        id: 3,
        name: '  ',
        amount: 10,
        price: 100
    }
];
let products: Products[];

const localStorageProducts = JSON.parse(localStorage.getItem('products') as string)
if(localStorageProducts?.length) {
    products = localStorageProducts;
}
else {
    localStorage.setItem('products', JSON.stringify(mockProducts))
    products = mockProducts
}

const setProducts = (val: Products[]) => {
    products = val;
    localStorage.setItem('products', JSON.stringify(products))
}

export {
    products,
    setProducts
}