import {
    GridRowsProp,
    GridRowModesModel,
    GridRowModes,
    DataGrid,
    GridColDef,
    GridToolbarContainer,
    GridActionsCellItem,
    GridEventListener,
    GridRowId,
    GridRowModel,
    GridRowEditStopReasons,
    GridSlots,
} from "@mui/x-data-grid";
import AddIcon from "@mui/icons-material/Add";
import DeleteIcon from "@mui/icons-material/Delete";
import { products } from "../store";
import Button from "../components/Button";
import { Link } from "react-router-dom";
import { useEffect, useState } from "react";

export default function Products() {
    const [rowModesModel, setRowModesModel] = useState<GridRowModesModel>({});
    const [rows, setRows] = useState(products);

    interface EditToolbarProps {
        setRows: (newRows: (oldRows: GridRowsProp) => GridRowsProp) => void;
        setRowModesModel: (
            newModel: (oldModel: GridRowModesModel) => GridRowModesModel
        ) => void;
    }

    useEffect(() => {
        localStorage.setItem("products", JSON.stringify(rows));
    }, [rows]);

    function EditToolbar(props: EditToolbarProps) {
        const { setRows, setRowModesModel } = props;

        const handleClick = () => {
            const id = rows[rows.length - 1].id + 1;
            console.log(id);
            setRows((oldRows) => [
                ...oldRows,
                { id, name: "", price: 0, amount: 0 },
            ]);
            setRowModesModel((oldModel) => ({
                ...oldModel,
                [id]: { mode: GridRowModes.Edit, fieldToFocus: "name" },
            }));
        };

        return (
            <GridToolbarContainer>
                <Button
                    className="!text-[15px] !min-w-[10px]"
                    startIcon={<AddIcon />}
                    onClick={handleClick}
                    color="primary"
                >
                      
                </Button>
            </GridToolbarContainer>
        );
    }

    const handleDeleteClick = (id: GridRowId) => () => {
        setRows(rows.filter((row) => row.id !== id));
    };

    const columns: GridColDef<(typeof products)[number]>[] = [
        {
            field: "name",
            headerName: "",
            minWidth: 600,
            editable: true,
            cellClassName: "font-bold",
            headerClassName: "text-[45px]",
            hideable: true,
        },
        {
            field: "price",
            headerName: "",
            width: 280,
            cellClassName: "bg-green-500",
            headerClassName: "text-[45px]",
            editable: true,
            renderCell: ({ value }) => <>{value} .</>,
        },
        {
            field: "amount",
            headerName: "",
            width: 280,
            cellClassName: "bg-green-400",
            headerClassName: "text-[45px]",
            editable: true,
        },
        {
            field: "actions",
            type: "actions",
            headerName: "",
            minWidth: 250,
            headerClassName: "text-[45px]",
            cellClassName: "actions",
            getActions: ({ id }) => {
                return [
                    <GridActionsCellItem
                        icon={
                            <Button className="!text-[30px]" color="error">
                                
                            </Button>
                        }
                        label="Delete"
                        onClick={handleDeleteClick(id)}
                        color="inherit"
                    />,
                ];
            },
        },
    ];

    const processRowUpdate = (newRow) => {
        const updatedRow = { ...newRow };
        setRows(rows.map(row => row.id === updatedRow.id ? {...updatedRow} : row));
        
        return updatedRow;
    }

    const handleEdit = ({row}) => {
        console.log(row)
    }
    const handleRowModesModelChange = (newRowModesModel: GridRowModesModel) => {
        setRowModesModel(newRowModesModel);
      };

    return (
        <div className="flex flex-col">
            <Link to="/" className="p-3 max-w-[50px]">
                <Button className="!min-w-[200px]" fontSize="25px">
                    
                </Button>
            </Link>

            <DataGrid
                editMode="row"
                processRowUpdate={processRowUpdate}
                rowModesModel={rowModesModel}
                onRowModesModelChange={handleRowModesModelChange}
                rows={rows}
                columns={columns}
                sx={{
                    fontSize: "35px",
                }}
                slots={{
                    toolbar: EditToolbar as GridSlots["toolbar"],
                }}
                onCellEditStop={handleEdit}
                slotProps={{
                    toolbar: { setRows, setRowModesModel },
                }}
                hideFooterPagination
                disableRowSelectionOnClick
            />
        </div>
    );
}
